:root {
  --lime-green: hsl(163, 72%, 41%);
  --bright-red: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-background-start: hsl(210, 78%, 56%);
  --toggle-background-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-background: hsl(230, 17%, 14%);
  --dark-top-background: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-background: hsl(0, 0%, 100%);
  --light-top-background: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  /*The styling immediately below ensures that if you add padding to an element the total width will 
    include the padding and not be an addition to the padding */
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

/* The first property below overrides the default margin of webpages because by default a margin is added to 
the top and the buttom*/
h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

/* Importation for the breakpoints file in utils folder*/
.card {
  padding: 1.25rem;
}

.toggle__button {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--toggle);
}

